<template>
  
  <v-layout wrap

  >

    <v-flex xs12
        
    >

        <v-text-field
            v-model='email'
            label='Email'
            solo
            rounded
            :filled='!isFirefox()'
            dark
            
        >
        </v-text-field>

    </v-flex>

    <v-flex xs12>
        <v-text-field
        v-model='password'
        label='password'
        type='password'
        solo
        rounded
        :filled='!isFirefox()'
        dark
      >

      </v-text-field>
    
    </v-flex>

    <v-flex xs12 grow>
        <v-btn text color='info' light @click='onLogin' rounded width='100%' > login </v-btn>
    </v-flex>
     

      
  </v-layout>


</template>


<script>


import qs from 'qs'



export default {



   /* beforeRouteEnter( to,from, next ){

        alert('hi')

        console.log('yo')
        if(localStorage.email)
        {
            next('/')

        }
        else
            next()
    },*/

    mounted(){

        //alert(localStorage.email)

        if(this.$root.$data.$loggedIn == true)
        {
            this.$router.push('/clients')

        }
    },

    data(){
        return {
            isLoggedIn(){
                return this.$root.$data.$loggedIn
            },
            email: '',
            password: '',
            error: ''
        }
    },


    methods: {
        
        isFirefox(){

            return typeof InstallTrigger !== 'undefined'
        },
        onLogin(){

            axios({
                method: 'POST',
                baseURL: this.$config.baseUrl,
                url: '/login_f',
                withCredentials: true,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },

                data:  qs.stringify({
                    email: this.email,
                    password: this.password
                })

            },
               
            )
            .then( (r) => {
                if(r)
                {
                
                   // alert('yo')
                    
                   this.$root.$data.$loggedIn = true
                   // alert(r.data.email)
                  //  console.log(localStorage.email)

                  //alert(JSON.stringify(r.data))

                    this.$root.$data.$name = r.data.name

                    this.$nextTick( () => {
                        this.$router.push('/clients')
                    } )
                    

                 //   alert(JSON.stringify(r))


                    
                }
            } )
            .catch( (e) => {
                this.$root.$data.$loggedIn = false
                //this.$root.$data.$loggedIn = true
                this.error = 'Invalid Login'
            } )





            

        }

    }

}
</script>

