<template>
    <v-layout row pa-5>
    
        <v-flex xs12 class='white elevation-2' py-5 style='border-radius: 25px; '> 
                    <!-- DIALOG -->
                    <v-dialog v-model="dialog" :max-width="dynamicWidth" :key='dialog_key' >
                        <template v-slot:activator="{ on }" class='white' >
                            <v-layout row 
                                        :wrap='$vuetify.breakpoint.smAndDown' 
                                        :justify-end='$vuetify.breakpoint.lgAndUp' 
                                        :justify-center='$vuetify.breakpoint.smAndDown' pr-5    >
                                <v-flex :xs6='$vuetify.breakpoint.smAndDown' px-5 >
                                    <h1 class='display-1' style='opacity: .6;'> {{firstCharUpperCase($route.name)}} </h1>
                                </v-flex>
                                <v-flex xs12 lg5 shrink  :mb-5='$vuetify.breakpoint.smAndDown' > 
                                    <v-text-field single-line
                                        style='height: 14px !important;transform: scale(0.75);'
                                        rounded 
                                        v-model='search' 
                                        label='Search'
                                        outlined
                                        prepend-inner-icon='search'

                                        
                                         > </v-text-field>  
                                </v-flex>
                                <v-flex xs6 lg2 shrink :mt-5='$vuetify.breakpoint.smAndDown' >
                                    <v-btn medium  dark 
                                        color='black'
                                        class='hover-btn mb-2'
                                        style='background-image: linear-gradient(to right, #e53935 0%, #e35d5b 51%, #e53935 100%)'
                                    rounded @click.stop='++dialog_key; dialog = true; userMethod=methodsEnums.POST' >New Item</v-btn>
                                </v-flex>
                            </v-layout>
                        </template>

                
                <v-card>
                        
                    <v-card-title>
                    <span class="headline">{{ }}</span>
                    </v-card-title>

                    <!-- content -->

                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap v-if='userMethod == methodsEnums.POST || userMethod == methodsEnums.PATCH'>

                                <!-- form -->         
                                <v-layout wrap v-if='userMethod == methodsEnums.POST || userMethod == methodsEnums.PATCH'>

                            
                                        <v-flex xs12 sm6>
                                            <v-text-field 
                                                v-model="editedItem.stock" 
                                                label="Stock"
                                                v-validate="'required|decimal'"
                                                data-vv-name='stock'
                                                :error-messages="errors.collect('stock')"
                                            ></v-text-field>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-text-field 
                                                v-model="editedItem.avg_rate" 
                                                label="Rate"
                                                v-validate="'required|min:0|decimal'"
                                                data-vv-name='rate'
                                                :error-messages="errors.collect('rate')"
                                            ></v-text-field>
                                        </v-flex>

                                        <v-flex xs12 md6>
                                            <v-autocomplete 
                                                autocomplete="off"
                                                label='Project'
                                                :loading='!extras.projects.error && extras.projects.loading ? extras.projects.loading : false '
                                                :items='!extras.projects.loading && !extras.projects.error ? extras.projects.data : []'
                                                v-validate="'required'"
                                                data-vv-name='project'
                                                :error-messages="errors.collect('project')"
                                                v-model='editedItem.project_id'

                                                type='search'
                                                
                                            > </v-autocomplete>
                                        </v-flex>
                                         <v-flex xs6 >
                                            <v-autocomplete 
                                            autocomplete="off"
                                            label='Products'
                                            hide-selected
                                            :items='extras.products.data'
                                            v-validate="'required'"
                                            :data-vv-name='"product_name"'
                                            :error-messages='errors.collect("product_name_")'
                                            v-model='editedItem.product_id'

                                            type='search'

                                           
                                            
                                            > </v-autocomplete> 
                                        </v-flex>
                                        
                                    </v-layout>
                        
                            </v-layout>

                            <v-layout v-else-if='userMethod == methodsEnums.DELETE' wrap style='background: rgba(0,0,0,.15);border-radius: 12px'>
                                <v-flex    v-for='(item,index) in Object.values(this.editedItem)' 
                                    style=''
                                    class='pa-2'
                                > {{item}} </v-flex>

                            </v-layout>

                            <v-layout v-else-if='userMethod == methodsEnums.APPROVE' wrap >
                            
                            <h1 class='display-1'> Are you sure, you want to Approve this ? </h1>
                            

                                <v-flex    
                                    v-for='(item,index) in Object.values(this.editedItem)' 
                                    style='background: rgba(0,0,0,.15);border-radius: 12px'
                                    class='pa-2'

                                > 
                                    {{item}} 
                                    
                                </v-flex>

                                


                            </v-layout>
                        </v-container>



                </v-card-text>
                
                <!-- dialog actions -->
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <data-model 
                        :endpoint="endpoint" 
                        
                        @success='postSuccess();' @error='postError()'>
                        <div slot-scope="{ approve,remove, data: post_res, loading : post_loading, create, update, error }">
                            <v-btn color="blue darken-1" text @click="dialogClose"> Cancel </v-btn>
                            <v-btn color="blue darken-1" :loading='post_loading' text @click='dialogSave({functions: {create, approve, remove, update}})'> {{methodToStringPresent(userMethod)}} </v-btn>
                        </div>
                    </data-model>
                </v-card-actions>
            </v-card>


                    </v-dialog>

                    <!-- DATA -->

                    <data-list 
                        :endpoint="endpoint+'/product/'+$route.params.id" 
                        :key='dl_key' 
                        @success='listSuccess' 
                        @error='listError'

                    >
                        <div slot-scope="{ data , error, loading }">

                            
                            <v-data-table 

                                :sort-by.sync='table.sortBy'
                                :sort-desc.sync='table.sortDesc'
                                :loading='loading'
                                :headers='headers'
                                :items=' data ?  data.result : []'
                                :options.sync='options'

                                :search='search'
                                
                                class='elevation-0' 
                                :style="{ borderRadius: '15px !important' }"
                            >   
                                
                                <template v-slot:item.inventory_id="{item}">
                                        {{ item.inventory_id | inventory }}
                                </template>
                                
                                 <!-- <template v-slot:item.total_amount="props">
                                     {{ props.item.total_amount | currency }}
                                
                                </template> -->
                                
                                <!--<template v-slot:item.inventory_id="props">
                                    <router-link tag="div" :to="{
                                            name: 'inventory',
                                            params: { id :  props.item.inventory_id }
                                        }" 
                                        append class='clickable'>
                                        <div> {{ props.item.inventory_id }}</div>
                                    </router-link>
                                </template> -->
                                

                                <template v-slot:item.actions="{ item }">
                                    
                                    <v-icon
                                        small
                                        class="mr-2"
                                        @click="actionEditItem(item)"
                                    >
                                        edit
                                    </v-icon>
                                    <v-icon
                                        small
                                        @click="actionDeleteItem(item)"
                                    >
                                       delete
                                    </v-icon>
                                </template>
                                

                            </v-data-table>
                        </div>
                    </data-list>
        </v-flex>

    </v-layout>
</template>

<script>
import utilities from '@/components/mixins/utilities.js'
import DataList from '@/components/data/DataList'
import DataModel from '@/components/data/DataModel'

// import axios from 'axios'

import Vue from 'vue'

const methodsEnums = {
    GET : 0,
    POST : 1,
    PATCH : 2,
    DELETE : 3
}

Object.freeze(methodsEnums)

function parseRule(obj){
       let str = ''
       Object.keys(obj).forEach( (k,i) => {
              str += k
              if(obj[k].length > 0)
                     str += ':'  + obj[k].join(',').trim()
       
              str += '|'

       } )

       return str
}

export default {
    extends: utilities,
    components: {
        DataList,
        DataModel
    },
    data(){
        return {

            endpoint: 'inventory',

            extras: {
                    products: {
                        data: [
            
                        ],
                        loading: true,
                        error: false,
                        loaded: false
                    },
                    projects: {
                        loading: true,
                        error: false,
                        data: []
                    },

            },

            search: '',

            dialog: false,
            dialog_key: 0,
            dl_key: 55,
            dialog_loading: false,
            

            items: [],

            options: {},

            headers: [
                {
                    text: 'Inventory ID',
                    value: 'inventory_id'
                },
                {
                    text: 'Product name',
                    value: 'product_name'
                },

                {
                    text: 'Type',
                    value: 'inventory_type'
                },
                /*{
                    text: 'Godown Name',
                    value: 'godown_name'
                },*/
                {
                    text: 'Project Name',
                    value: 'project_name'
                },
                {
                    text: 'Stock',
                    value: 'stock'
                },
                {
                    text: 'Avg Rate',
                    value: 'avg_rate'
                },
               /* {
                    text: 'Total Amount',
                    value: 'total_amount'
                }, 
               
                
                {
                    text: 'Unit',
                    value: 'unit_name'
                }, */
                {
                    text: 'Actions',
                    value: 'actions'
                } 
            ],

            enums: {
                type: [
                    'purchase',
                    'selling',
                    'trade'

                ]
            },

            editedIndex: -1,


            editedItem: {
            
            },
            defaultItem: {
                project_id: null,
                product_id: null,
                stock: null,
                avg_rate: null,
                type : 1
            },

            snackbar: {
                visible: false,
                state: false,
                message : ''

            },
            methodsEnums : methodsEnums,
            userMethod : 0,
            table: {
                sortBy: '',
                sortDesc: false
            }

            

        }},

    computed: {
        dynamicWidth(){
            switch(this.$vuetify.breakpoint.name)
            {
                case 'xs': return '100%'
                case 'sm': return '100%'
                case 'md': return '80%'
                case 'lg': return '50%'
                case 'xl': return '50%'
            }
        },
        formTitle () {
            //return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
             switch(this.userMethod){
                
                case this.methodsEnums.POST : return 'New Item';
                case this.methodsEnums.PATCH : return 'Update Item';
                case this.methodsEnums.DELETE : return 'Delete Item';
                default : return 'Invalid State'
            }
        },
    },

    watch: {
        dialog (val) {
            val || this.dialogClose()
        },


        /*
        
        'options.page': {
            handler () {
                const { page } = this.options
                
                this.getItems('/projects',{
                    limit: 10,
                    offset: 5
                })
            },
        },
        'options.sortBy': {
            handler (newVal, oldVal) {

                const { sortBy, sortDesc } = this.options
                this.table.sortBy = sortBy
                this.table.sortDesc = sortDesc
            }
        },
        */



    },

    async mounted(){

        
        //alert(JSON.stringify(this.generateHeaders(this.schema)))
        
        
    },

    async created(){
        this.getProjects()
        this.getProducts()
    },
    
    methods: {

        async getProjects(){
                
                let projects = await axios({
                method: 'GET',
                baseURL: this.$config.baseUrl,
                url: 'projects',
                params: {
                        //client_id : this.form.client_id
                    }
                })

 

                if(projects.data.results)
                {
                    this.extras.projects.loading = false
                    this.extras.projects.data = this.generateItems(projects.data.results,'project_id')
                }
            },

            async getProducts(){
                
                let products = await axios({
                method: 'GET',
                baseURL: this.$config.baseUrl,
                url: 'products',
                params: {
                        //client_id : this.form.client_id
                        //project_id: this.form.sender
                    }
                })



                if(products.data.results)
                {
                   
                    this.extras.products.loading = false
                    this.extras.products.data = this.generateItems(products.data.results,'product_id')
                    
                }
            },

        async getItems(endpoint,data) {

            return await axios({
                method: 'GET',
                baseURL: this.$config.baseUrl,
                url: endpoint,
                data: data
            })






        },

        generateInstance(){

        },
        listSuccess (r){
            this.items = r.data.results
        },
        listError(e){
            this.items = []
        },
        
        generateItems(input,key){
            let temp = []
            temp.push({
                text: 'none',
                value: null
            })
            input.forEach( (item,index) => {
                temp.push({
                    text: item.name,
                    value: item[key]
                    })
            } )

        
            return temp
        },

        keyHack(){
            return ++this.dialog_key
        },
        
        generateIterable(input){
            let temp = []
            
            for( let i in input  )
            {
                let obj = input[i]
                if(!obj.ignore)
                    temp.push(obj)
            }

            return temp
        },
        
        generateHeaders(input){

            let temp = []
            for( var i in input )
            {
               let { text, value } = input[i]
                temp.push({text,value})
            }
            temp.push({text: 'Actions',value: 'actions'})
            return  temp
        },


        actionEditItem (item) {

            this.userMethod = this.methodsEnums.PATCH
            
            this.editedItem = Object.assign({}, item) // SET selected item to editedItem
            this.editedItem.unit_id = String(item.unit_id)



            this.editedItem.type = String(item.type)


            this.dialog = true // start the dialog
        
        },

        actionDeleteItem (item) {

            this.userMethod = this.methodsEnums.DELETE
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },


        dialogClose(){

            this.dialog = false

            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            }, 300)


        },

        dialogSave (dialog) {

           // dialog.functions.update(this.editedItem)

            //console.log(dialog.functions)
            

           switch(this.userMethod)
           {
                case this.methodsEnums.POST :  
                       this.$validator.validateAll().then( (r) => {
                            if(r)
                                dialog.functions.create(this.editedItem);
                        } );
                        break;
                case this.methodsEnums.PATCH :  
                        this.$validator.validateAll().then( (r) => {
                            if(r)
                                dialog.functions.update(this.editedItem);
                        } );
                        break;
                case this.methodsEnums.DELETE :  dialog.functions.remove( { inventory_id: this.editedItem.inventory_id } ); break;

        
           }
           
            
            //this.dialogClose()
        },

        postSuccess(){
        
            this.snackbar.message = `Successfully ${this.methodToString(this.userMethod)} a project`
            this.snackbar.state = true
            this.snackbar.visible = true

            ++this.dl_key
            this.dialogClose()
        },
        postError(){

            this.snackbar.state = false
            this.snackbar.visible = true
            this.snackbar.message = 'Something Went Wrong. Try again later' 

        },

        methodToString(choice){
            switch(choice)
            {
                case this.methodsEnums.POST : return 'added';
                case this.methodsEnums.PATCH : return 'updated';
                case this.methodsEnums.DELETE : return 'deleted';
            }
        },
        methodToStringPresent(choice){
            switch(choice)
            {
                case this.methodsEnums.POST : return 'add';
                case this.methodsEnums.PATCH : return 'update';
                case this.methodsEnums.DELETE : return 'delete';
            }
        }
    },
}
</script>

<style>


table {
    border-collapse: separate !important;
    border-spacing: 0px 0px !important;
}

tr {
    bosx-shadow: 0px 0px 5px 0px rgba(0,0,0,.1);
}

thead > tr {
    background: s#CCAEB1;
}

tbody > tr:nth-child(even){
    background: #fff;

   
}
tbody > tr:nth-child(odd){
    background: linear-gradient(to right, rgba(237, 66, 100, .1), rgba(255, 237, 188, .2)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */;
   
}

tbody > tr > td:fsirst-child {
     border-radius: 15px 0 0 15px;
}
tbody > tr > td:lasst-child {
     border-radius: 0 15px 15px 0px;
}

.hover-btn:hover {
     background-position: right center !important;
    
}

table {
}

.text-start{
     color: #2e343b;
     fot-size: .9em !important;
}

</style>

